div#root {
    width: 100%;
    min-height: 100vh;
}

.turing-machine {
    width: 100vw;
    min-height: 100vh;
    position: relative;
}

.board-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: tan;
    background-image: linear-gradient(#d2b48c, 50%, #c89f76);
}

.options {
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    display: flex;
    gap: 1rem;
}

button {
    display: flex;
    align-items: center;
    font-family: "roboto";
    background-image: linear-gradient(var(--light-metallic), 70%, var(--dark-metallic) 120%);
    border: none;
    cursor: pointer;
    padding: .5rem;
    border-radius: 10px;
    --filter: var(--blue-light-filter);
    --font-color: var(--blue-light-color);
    box-shadow: 0px 4px 10px -1px black;
    transition: transform .1s linear;
}

button img {
    /* width: clamp(20px, 15px + 1vw, 25px); */
    width: 25px;
    filter: var(--filter);
    transform: var(--transform);
}

button:hover {
    --filter: var(--orange-light-filter);
    transform: scale(1.1);
    --font-color: var(--orange-light-color);
}

button.settings:hover img {
    animation-name: rotate;
    animation-duration: .1s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
}

button:active {
    box-shadow: 0px 4px 10px -1px inset black;
}

.speed::before {
    display: inline-block;
    margin-right: .2rem;
    font-weight: bold;
    font-size: 1rem;
    color: var(--font-color);
}

.speed.stop::before {
    content: "0x";
}

.speed.one::before {
    content: "1x";
}

.speed.two::before {
    content: "2x";
}

.speed.five::before {
    content: "5x";
}

.speed.max::before {
    content: "500x";
}

.options-modal {
    width: fit-content;
    position: fixed;
    top: 20%;
    right: 0;
    left: 0;
    margin: 0 auto;
    background-color: var(--dark-metallic);
    background-image: linear-gradient(var(--light-metallic), var(--dark-metallic));
    padding: 1rem;
    box-shadow: 0px 5px 10px 0px black;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

.options-modal.hidden {
    visibility: hidden;
}

.options-modal button {
    color: var(--blue-light-color);
    font-weight: bold;
    font-size: 1rem;
}

.options-modal button:hover, .options-modal button:active {
    color: var(--orange-light-color);
}

.exit-wrapper {
    display: flex;
    justify-content: end;
    width: 100%;
}

.exit-btn img {
    width: 20px;
}

.new-ant-wrapper {
    display: flex;
    gap: 1rem;
}

select {
    background-color: var(--light-metallic);
    border: none;
    font-size: 1rem;
    font-weight: bold;
    outline: none;
    border-radius: 10px;
    background-image: linear-gradient(var(--light-metallic), 70%, var(--dark-metallic) 120%);
    box-shadow: 0px 4px 5px -1px black;
    cursor: pointer;
}

.current-ants {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 1rem;
    align-items: center;
    height: 155px;
    overflow: auto;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    scrollbar-color: var(--blue-light-color) rgb(0, 0, 0, 0);
    scrollbar-gutter: stable;
}

.ant-card {
    display: flex;
    width: 70%;
    align-items: center;
}

.ant-card p {
    flex: 1;
    font-weight: bold;
}

.ant-card button {
    padding: 0;
    box-shadow: none;
    background: none;
}

@keyframes rotate {
    0% {transform: rotate(0);}
    100% {transform: rotate(30deg);}
}`, "",{"version":3,"sources":["webpack://./src/styles/index.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,iCAAiC;IACjC,yBAAyB;IACzB,wBAAwB;IACxB,2BAA2B;IAC3B,6BAA6B;IAC7B,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,wBAAwB;IACxB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;IACtB;;;;;;;;iBAQa;IACb;;;;;;;;iBAQa;AACjB;;AAEA;IACI,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,qBAAqB;IACrB,wDAAwD;AAC5D;;AAEA;IACI,eAAe;IACf,WAAW;IACX,aAAa;IACb,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,qBAAqB;IACrB,wFAAwF;IACxF,YAAY;IACZ,eAAe;IACf,cAAc;IACd,mBAAmB;IACnB,kCAAkC;IAClC,qCAAqC;IACrC,mCAAmC;IACnC,gCAAgC;AACpC;;AAEA;IACI,0CAA0C;IAC1C,WAAW;IACX,qBAAqB;IACrB,2BAA2B;AAC/B;;AAEA;IACI,oCAAoC;IACpC,qBAAqB;IACrB,uCAAuC;AAC3C;;AAEA;IACI,sBAAsB;IACtB,uBAAuB;IACvB,6BAA6B;IAC7B,iCAAiC;AACrC;;AAEA;IACI,yCAAyC;AAC7C;;AAEA;IACI,qBAAqB;IACrB,mBAAmB;IACnB,iBAAiB;IACjB,eAAe;IACf,wBAAwB;AAC5B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;AACj